import React from 'react'
import styled from 'styled-components'
import PageTitle from '../components/PageTitle'
import {
  borderColor,
  tertiaryTextColor,
  secondaryTextColor,
} from '../config/theme/colors'
import { large, medium, small, xxSmall } from '../config/theme/spacings'
import IconFacebook from '../icons/Facebook'
import IconTwitter from '../icons/Twitter'
import IconLinkedIn from '../icons/LinkedIn'
import IconInstagram from '../icons/Instagram'
import { media } from '../config/theme/breakpoints'

const StyledUl = styled.ul`
  padding: 0;
`

const StyledLi = styled.li`
  display: flex;
  flex-direction: column;
  margin-bottom: ${large};
  padding: ${small};
  border: 1px solid ${borderColor};
  border-radius: 3px;
  list-style-type: none;

  ${media.medium`
    padding: ${large};
  `};
`

const ItemHeader = styled.div`
  display: flex;
  flex-direction: row;
`

const Avatar = styled.img`
  width: 100px;
  height: 100px;
  border-radius: 100%;
  object-fit: cover;
`

const MemberDetails = styled.div`
  flex-direction: row;
  flex: 1;
  margin-left: ${medium};
`

const MemberName = styled.h2`
  margin: ${xxSmall};
`

const MemberSubtitle = styled.h3`
  margin: ${xxSmall};
  color: ${secondaryTextColor};
`

const MemberLocation = styled.span`
  margin: ${xxSmall};
  font-weight: normal;
  color: ${tertiaryTextColor};
`

const ItemBody = styled.div``

const MemberBio = styled.p`
  color: ${secondaryTextColor};
`

const ItemFooter = styled.div``

const SocialLink = styled.a`
  padding: 4px 11px;
  font-size: 20px;
`

const Input = styled.input`
  outline: none;
  padding: 10px;
  border: 1px solid ${borderColor};
  width: 100%;
  box-sizing: border-box;
  border-radius: 3px;
`

const MemberItem = ({ memberData }) => (
  <StyledLi>
    <ItemHeader>
      <Avatar atl={`${memberData.name}'s Photo`} src={memberData.photo} />
      <MemberDetails>
        <MemberName>{memberData.name}</MemberName>
        <MemberSubtitle>{memberData.subtitle}</MemberSubtitle>
        {memberData.location && (
          <MemberLocation>{memberData.location}</MemberLocation>
        )}
      </MemberDetails>
    </ItemHeader>
    <ItemBody>
      <MemberBio>{memberData.bio}</MemberBio>
    </ItemBody>
    <ItemFooter>
      {memberData.instagramprofile && (
        <SocialLink href={memberData.instagramprofile}>
          <IconInstagram />
        </SocialLink>
      )}
      {memberData.twitterprofile && (
        <SocialLink href={memberData.twitterprofile}>
          <IconTwitter />
        </SocialLink>
      )}
      {memberData.facebookprofile && (
        <SocialLink href={memberData.facebookprofile}>
          <IconFacebook />
        </SocialLink>
      )}
      {memberData.linkedinprofile && (
        <SocialLink href={memberData.linkedinprofile}>
          <IconLinkedIn />
        </SocialLink>
      )}
    </ItemFooter>
  </StyledLi>
)

class MembersPage extends React.Component {
  state = {
    initialState: this.props.data.allMember.edges,
    userList: this.props.data.allMember.edges,
    searchQuery: '',
  }

  handleSearch = e => {
    const query = e.target.value.trim().toLowerCase()
    const { userList, initialState } = this.state

    const filteredList = userList.filter(
      user =>
        user.node.name.toLowerCase().indexOf(query) > -1 ||
        (user.node.location || '').toLowerCase().indexOf(query) > -1 ||
        user.node.bio.toLowerCase().indexOf(query) > -1 ||
        user.node.subtitle.toLowerCase().indexOf(query) > -1
    )

    const renderList = query ? filteredList : initialState

    this.setState({
      userList: renderList,
    })
  }

  render() {
    const { userList } = this.state

    return (
      <div>
        <label htmlFor="search-input">Search Members</label>
        <Input
          placeholder="Serach members"
          id="search-input"
          onChange={this.handleSearch}
        />
        <StyledUl>
          {userList.length ? (
            userList.map((member, index) => (
              <MemberItem key={index} memberData={member.node} />
            ))
          ) : (
            <div> No members found </div>
          )}
        </StyledUl>
      </div>
    )
  }
}

// eslint-disable-next-line no-undef
export const memberQuery = graphql`
  query Members {
    allMember {
      edges {
        node {
          photo
          name
          subtitle
          bio
          email
          location
          instagramprofile
          facebookprofile
          twitterprofile
          linkedinprofile
        }
      }
    }
  }
`

export default MembersPage



